<template>
	<view class="comment-message-page">

		
		<!-- ËØÑËÆ∫ÂàóË°® -->
		<scroll-view class="comment-list" scroll-y>
			<!-- Á©∫Áä∂ÊÄÅ -->
			<view v-if="commentList.length === 0" class="empty-state">
				<text class="empty-icon">üí¨</text>
				<text class="empty-text">ÊöÇÊó†ËØÑËÆ∫Ê∂àÊÅØ</text>
			</view>
			
			<!-- ËØÑËÆ∫È°π -->
			<view 
				v-for="(comment, index) in commentList" 
				:key="index" 
				class="comment-item"
				@click="goToPostDetail(comment)"
			>
				<!-- Â∑¶‰æßÂ§¥ÂÉè -->
				<image 
					class="avatar" 
					:src="comment.avatar || '/static/default-avatar.png'" 
					mode="aspectFill"
				></image>
				
				<!-- Âè≥‰æßÂÜÖÂÆπÂå∫Âüü -->
				<view class="comment-content">
					<!-- Áî®Êà∑ÊòµÁß∞ -->
					<text class="nickname">{{ comment.nickname }}</text>
					
					<!-- Êó∂Èó¥ÂíåÁ±ªÂûã -->
					<view class="time-row">
						<text class="time">{{ formatTime(comment.created_at) }}</text>
						<text class="comment-type">
							{{ comment.comment_type === 'post' ? 'ËØÑËÆ∫‰∫Ü‰Ω†ÁöÑÂ∏ñÂ≠ê' : 'ÂõûÂ§ç‰∫Ü‰Ω†ÁöÑËØÑËÆ∫' }}
						</text>
					</view>
					
					<!-- ËØÑËÆ∫ÂÜÖÂÆπ -->
					<view class="content-box">
						<text class="content-text">{{ comment.content }}</text>
					</view>
					
					<!-- ÂéüÂ∏ñÂ≠êÂÜÖÂÆπÈ¢ÑËßà -->
					<view class="post-preview" v-if="comment.post_content">
						<text class="preview-label">ÂéüÂ∏ñÂ≠êÔºö</text>
						<text class="preview-text">{{ comment.post_content }}</text>
						<!-- Â∏ñÂ≠êÂõæÁâáÈ¢ÑËßà -->
						<image 
							v-if="getFirstImage(comment.post_images)" 
							class="preview-image" 
							:src="getFirstImage(comment.post_images)" 
							mode="aspectFill"
						></image>
					</view>
				</view>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	import { mapState } from 'vuex'
	
	export default {
		data() {
			return {
				commentList: []
			};
		},
		
		computed: {
			...mapState('m_user', ['openid', 'token'])
		},
		
		methods: {
			// ËøîÂõû‰∏ä‰∏ÄÈ°µ
			goBack() {
				uni.navigateBack();
			},
			
			// Ë∑≥ËΩ¨Âà∞Â∏ñÂ≠êËØ¶ÊÉÖÈ°µ
			goToPostDetail(comment) {
				if (comment.post_images > 0) {
					uni.navigateTo({
						url: '/subpkg/topic-detail/topic-detail?id=' + comment.post_id
					});
				}
				else {
					uni.navigateTo({
						url: '/subpkg/post-detail/post-detail?id=' + comment.post_id
					})
				}
				
			},
			
			// Ê†ºÂºèÂåñÊó∂Èó¥
			formatTime(timeString) {
				const now = new Date();
				const time = new Date(timeString);
				const diff = Math.floor((now - time) / 1000); // Áßí
				
				if (diff < 60) {
					return 'ÂàöÂàö';
				} else if (diff < 3600) {
					return Math.floor(diff / 60) + 'ÂàÜÈíüÂâç';
				} else if (diff < 86400) {
					return Math.floor(diff / 3600) + 'Â∞èÊó∂Ââç';
				} else if (diff < 604800) {
					return Math.floor(diff / 86400) + 'Â§©Ââç';
				} else {
					const year = time.getFullYear();
					const month = String(time.getMonth() + 1).padStart(2, '0');
					const day = String(time.getDate()).padStart(2, '0');
					return `${year}-${month}-${day}`;
				}
			},
			
			// Ëé∑ÂèñÂ∏ñÂ≠êÁ¨¨‰∏ÄÂº†ÂõæÁâá
			getFirstImage(imagesString) {
				if (!imagesString) return '';
				try {
					const images = JSON.parse(imagesString);
					return images && images.length > 0 ? images[0] : '';
				} catch (e) {
					return '';
				}
			},
			
			// Ëé∑ÂèñËØÑËÆ∫Ê∂àÊÅØÂàóË°®
			async getCommentMessages() {
				try {
					uni.showLoading({ title: 'Âä†ËΩΩ‰∏≠...' });
					
					const { data: res } = await uni.$http.get('/users/comment-message', {
						openid: this.openid
					});
					
					uni.hideLoading();
					
					if (res.meta.status === 200) {
						this.commentList = res.message;
					} else {
						uni.showToast({
							title: res.meta.msg || 'Ëé∑ÂèñÂ§±Ë¥•',
							icon: 'none'
						});
					}
				} catch (error) {
					uni.hideLoading();
					console.error('Ëé∑ÂèñËØÑËÆ∫Ê∂àÊÅØÂ§±Ë¥•:', error);
					uni.showToast({
						title: 'ÁΩëÁªúÈîôËØØ',
						icon: 'none'
					});
				}
			}
		},
		
		onLoad() {
			// Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
			if (!this.token) {
				uni.showToast({
					title: 'ËØ∑ÂÖàÁôªÂΩï',
					icon: 'none'
				});
				setTimeout(() => {
					uni.navigateBack();
				}, 1500);
				return;
			}
			
			// Ëé∑ÂèñËØÑËÆ∫Ê∂àÊÅØ
			this.getCommentMessages();
		}
	}
</script>

<style lang="scss">
.comment-message-page {
	min-height: 100vh;
	background-color: #f5f5f5;
}

/* È°∂ÈÉ®ÂØºËà™Ê†è */
.header {
	position: sticky;
	top: 0;
	z-index: 100;
	display: flex;
	align-items: center;
	padding: 20rpx 32rpx;
	background-color: #ffffff;
	border-bottom: 1rpx solid #e5e5e5;
	
	.back-btn {
		margin-right: 20rpx;
		font-size: 36rpx;
		color: #333333;
	}
	
	.title {
		font-size: 36rpx;
		font-weight: bold;
		color: #333333;
	}
}

/* ËØÑËÆ∫ÂàóË°® */
.comment-list {
	height: calc(100vh - 80rpx);
	padding: 20rpx 0;
}

/* Á©∫Áä∂ÊÄÅ */
.empty-state {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 200rpx 0;
	
	.empty-icon {
		font-size: 120rpx;
		margin-bottom: 20rpx;
	}
	
	.empty-text {
		font-size: 28rpx;
		color: #999999;
	}
}

/* ËØÑËÆ∫È°π */
.comment-item {
	display: flex;
	padding: 24rpx 32rpx;
	background-color: #ffffff;
	margin-bottom: 20rpx;
	
	&:active {
		background-color: #f8f8f8;
	}
	
	/* Â§¥ÂÉè */
	.avatar {
		width: 80rpx;
		height: 80rpx;
		border-radius: 50%;
		margin-right: 20rpx;
		flex-shrink: 0;
	}
	
	/* Âè≥‰æßÂÜÖÂÆπ */
	.comment-content {
		flex: 1;
		display: flex;
		flex-direction: column;
		
		/* ÊòµÁß∞ */
		.nickname {
			font-size: 30rpx;
			font-weight: 500;
			color: #333333;
			margin-bottom: 8rpx;
		}
		
		/* Êó∂Èó¥Ë°å */
		.time-row {
			display: flex;
			align-items: center;
			margin-bottom: 16rpx;
			
			.time {
				font-size: 24rpx;
				color: #999999;
				margin-right: 16rpx;
			}
			
			.comment-type {
				font-size: 24rpx;
				color: #666666;
			}
		}
		
		/* ËØÑËÆ∫ÂÜÖÂÆπ */
		.content-box {
			padding: 20rpx;
			background-color: #f8f8f8;
			border-radius: 12rpx;
			margin-bottom: 16rpx;
			
			.content-text {
				font-size: 28rpx;
				color: #333333;
				line-height: 1.6;
			}
		}
		
		/* ÂéüÂ∏ñÂ≠êÈ¢ÑËßà */
		.post-preview {
			display: flex;
			align-items: center;
			padding: 16rpx;
			background-color: #fafafa;
			border-radius: 8rpx;
			border-left: 4rpx solid #4facfe;
			
			.preview-label {
				font-size: 24rpx;
				color: #999999;
				margin-right: 8rpx;
				flex-shrink: 0;
			}
			
			.preview-text {
				flex: 1;
				font-size: 26rpx;
				color: #666666;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
				margin-right: 16rpx;
			}
			
			.preview-image {
				width: 80rpx;
				height: 80rpx;
				border-radius: 6rpx;
				flex-shrink: 0;
			}
		}
	}
}
</style>
